
.theme-panel {
  background-color: var(--theme-background-secondary);
  border: 1px solid var(--theme-border-light);
  border-radius: 6px;
  padding: 8px 14px;
  margin-bottom: 15px;
}

/* 主题信息 */
.theme-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;

  .info-content {
    h3 {
      margin: 0;
      font-size: 0.95em;
      color: var(--theme-text-secondary);
      font-weight: 700;
      display: flex;
      align-items: center;
      gap: 4px;
    }

    .theme-name {
      font-weight: 400;
      color: var(--theme-text-muted);
    }

    .customized-tag {
      font-weight: 400;
      color: var(--theme-button-bg);
    }
  }

  .toggle-btn {
    padding: 6px 12px;
    background-color: var(--theme-button-bg);
    color: var(--theme-button-text);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.85em;
    transition: background-color 0.2s ease;
    white-space: nowrap;

    &:hover {
      background-color: var(--theme-button-bg-hover);
    }

    &.active {
      background-color: var(--theme-button-bg-hover);
    }
  }
}

/* 编辑面板 */
.editing-panel {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid var(--theme-border-light);
}

.group-selector {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 12px;

  .group-btn {
    padding: 6px 10px;
    background-color: var(--theme-background-tertiary);
    color: var(--theme-text-secondary);
    border: 1px solid var(--theme-border-light);
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.8em;
    transition: all 0.2s ease;

    &:hover {
      background-color: var(--theme-title-bg-hover);
      border-color: var(--theme-border-dark);
    }

    &.active {
      background-color: var(--theme-button-bg);
      color: var(--theme-button-text);
      border-color: var(--theme-button-bg);
    }
  }
}

.color-editor {
  margin-bottom: 12px;

  h4 {
    margin: 0 0 10px 0;
    font-size: 0.9em;
    color: var(--theme-text-secondary);
    font-weight: 600;
  }
}

.color-items {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 350px;
  overflow-y: auto;
  padding-right: 6px;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--theme-border-light);
    border-radius: 3px;

    &:hover {
      background: var(--theme-border-dark);
    }
  }
}

.color-item {
  display: flex;
  flex-direction: column;
  gap: 6px;

  label {
    font-size: 0.8em;
    color: var(--theme-text-secondary);
    font-weight: 600;
  }

  .input-group {
    display: flex;
    gap: 8px;
    align-items: center;

    .color-input {
      width: 50px;
      height: 32px;
      border: 1px solid var(--theme-border-light);
      border-radius: 4px;
      cursor: pointer;
    }

    .text-input {
      flex: 1;
      padding: 6px 8px;
      border: 1px solid var(--theme-border-light);
      border-radius: 4px;
      font-size: 0.8em;
      color: var(--theme-text-primary);
      background-color: var(--theme-background-tertiary);

      &:focus {
        outline: none;
        border-color: var(--theme-button-bg);
      }
    }

    .slider {
      flex: 1;
      cursor: pointer;
    }

    .value-display {
      min-width: 45px;
      font-size: 0.8em;
      color: var(--theme-text-muted);
      text-align: right;
    }
  }
}

/* 操作按钮 */
.actions {
  display: flex;
  gap: 8px;
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid var(--theme-border-light);

  button {
    flex: 1;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    font-size: 0.85em;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
  }

  .btn-primary {
    background-color: var(--theme-button-bg);
    color: var(--theme-button-text);

    &:hover {
      background-color: var(--theme-button-bg-hover);
    }
  }

  .btn-secondary {
    background-color: var(--theme-background-secondary);
    color: var(--theme-text-secondary);
    border: 1px solid var(--theme-border-light);

    &:hover {
      background-color: var(--theme-title-bg-hover);
      border-color: var(--theme-border-dark);
    }
  }
}
